import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import SubscribeForm from '../components/subscribeForm.js'
require('tachyons')

const Header = () => (
  <div>
    <div>
      <h1>
        <Link to="/" className='dim fw6 moon-gray avenir measure f4 no-underline hover'>
          taylor listens
        </Link>
      </h1>
    </div>
  </div>
)

const TemplateWrapper = ({ children }) => (
  <div className='mh2'>
    <Helmet
      title="taylor listens"
      meta={[
        { name: 'description', content: 'Taylor Bell writes about code (mostly JavaScript), music, and whatever else. A reverse memory hole.' },
        { name: 'keywords', content: 'javascript, react, code, music' },
      ]}
    />
    <Header />
    <div>
      {children()}
    </div>
    <div className='mt5 pv3 w-50 flex center' style={{borderTop: '3px solid rgba(0, 0, 0, 0.05)'}}>
      <SubscribeForm />
    </div>
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper



